.page-template-retailer,
.page-template-retailer-2 {

	#content {
		background: #F7F7F7;
	}

	.side-title {
		display: none;
	}

	h2.page-subtitle {
		margin-bottom: 15px;
		margin-left: 150px;
		margin-top: 50px;

		& + span {
			background: #7C7C7C;
			display: block;
			height: 2px;
			margin-bottom: 40px;
			margin-left: 150px;
			width: 75px;
		}

		@media screen and (max-width: 768px) {
			margin-left: 4%;

			& + span {
				margin-left: 4%;
			}
		}
	}

	#google-map {
		margin-top: -50px;
	}

	footer {
		margin-top: 125px;
	}
}