.social__post {
	position: relative;

	& > div {
		background-size: cover;
		height: 520px;
		position: absolute;
		width: calc(50% - 100px);
		z-index: 99;

		@media screen and (max-width: 992px) {
			position: static;
			width: 100%;
		}

		.border-box {
			border: 2px solid rgba(255,255,255,0.3);
			height: calc(100% - 20px);
			margin: 10px;
			width: calc(100% - 20px);
		}

		i {
			color: white;
			font-size: 20px;
			left: 30px;
			position: absolute;
			top: 20px;
		}

		.info {
			margin-top: 365px;
			padding: 0 20px 0 60px;
			position: relative;
			top: -100%;

			@media screen and (max-width: 992px) {
				top: calc(-100% - 75px);
			}

			h2 {
				color: white;
				font-size: 30px;
			}

			h3 {
				color: #DDDDDD;
			}
		}
	}
}