.page-template-checkout {

	.woocommerce-message {
		display: none;
	}

	form:not(.main-menu__search__form) {
		background: white;
		margin-bottom: 100px;
		margin-left: 7%;
		margin-top: -50px;
		max-width: 1300px;
		padding: 75px;
		position: relative;
		width: 86%;
		z-index: 999;

		@media screen and (max-width: 550px) {
			padding: 25px;
		}

		@media screen and (min-width: 1511px) {
			margin-left: auto;
			margin-right: auto;
		}

		h2 {
			margin-bottom: 18px;
		}

		.form-row {
			margin-bottom: 10px;
		}

		p {
			padding: 0;

			@media screen and (max-width: 550px) {
				width: 100% !important;
			}
		}

		label {
			display: none !important;
		}

		.select2-choice {
			color: #878787 !important;
			font-family: "Open Sans",sans-serif !important;
			font-size: 16px;
			font-weight: normal;
			letter-spacing: normal;
			padding: 3px 20px !important;
			text-transform: capitalize;
		}

		.select2-container a {
			-webkit-appearance: none;
			appearance: none;
			background: #F7F7F7;
			border: 1px solid #E6E6E6 !important;
			border-radius: 0;
			font-family: $font-copy;
			font-weight: normal;
			padding: 7px 15px;
		}
	}

	.place-order {
		text-align: center;
	}
}